import React, { useState, useRef, useEffect } from 'react'
import { useWeb3React } from '@web3-react/core'
import { Button } from '@pancakeswap-libs/uikit'
import BigNumber from 'bignumber.js'
import { FarmWithStakedValue } from 'views/Farms/components/FarmCard/FarmCard'
import { getBalanceNumber } from 'utils/formatBalance'
import { useHarvest } from 'hooks/useHarvest'
import useI18n from 'hooks/useI18n'
import { useFarmUser, usePriceCakeBusd } from 'state/hooks'
import { useCountUp } from 'react-countup'
import useStake from '../../../../../hooks/useStake'

import { ActionContainer, ActionTitles, Title, Subtle, ActionContent, Earned, Staked } from './styles'

const HarvestAction: React.FunctionComponent<FarmWithStakedValue> = ({ pid, userData }) => {
  const { earnings } = useFarmUser(pid)
  const cakePrice = usePriceCakeBusd()
  let rawEarningsBalance = 0
  let earningsBusd = 0
  let displayBalance = '?'

  if (earnings) {
    rawEarningsBalance = getBalanceNumber(earnings)
    earningsBusd = new BigNumber(rawEarningsBalance).multipliedBy(cakePrice).toNumber()
    displayBalance = rawEarningsBalance.toLocaleString()
  }

  const [pendingTx, setPendingTx] = useState(false)
  const { onReward } = useHarvest(pid)
  const { onStake } = useStake(pid)
  const TranslateString = useI18n()

  const { countUp, update } = useCountUp({
    start: 0,
    end: earningsBusd,
    duration: 1,
    separator: ',',
    decimals: 3,
  })
  const updateValue = useRef(update)

  useEffect(() => {
    updateValue.current(earningsBusd)
  }, [earningsBusd, updateValue])

  return (
    <ActionContainer>
      <ActionTitles>
        <Title>TMDS </Title>
        <Subtle>{TranslateString(739, 'EARNED')}</Subtle>
      </ActionTitles>
      <ActionContent>
        <div>
          <Earned>{displayBalance}</Earned>
          <Staked>≈{countUp} USD</Staked>
        </div>
        {pid === 8 ? (
          <Button
            disabled={rawEarningsBalance === 0 || pendingTx}
            onClick={async () => {
              setPendingTx(true)
              await onStake(earnings.toString())
              setPendingTx(false)
            }}
            ml="6px"
          >
            {TranslateString(704, 'Compound')}
          </Button>
        ) : null}
        <Button
          disabled={rawEarningsBalance === 0 || pendingTx}
          onClick={async () => {
            setPendingTx(true)
            await onReward()
            setPendingTx(false)
          }}
          ml="6px"
        >
          {TranslateString(562, 'Harvest')}
        </Button>
      </ActionContent>
    </ActionContainer>
  )
}

export default HarvestAction
