import React from 'react'
import styled from 'styled-components'
import ApyButton from 'views/Farms/components/FarmCard/ApyButton'
import { Address, FarmConfig, QuoteToken } from 'config/constants/types'
import BigNumber from 'bignumber.js'
import useI18n from 'hooks/useI18n'

export interface AprProps {
  value: number
  multiplier: string
  lpLabel: string
  quoteTokenAdresses: Address
  quoteTokenSymbol: QuoteToken
  tokenAddresses: Address
  cakePrice: BigNumber
  originalValue: BigNumber
  hideButton?: boolean
  farm: FarmConfig
}

const Container = styled.div`
  display: flex;
  align-items: center;
  color: ${({ theme }) => theme.colors.text};

  button {
    width: 20px;
    height: 20px;

    svg {
      path {
        fill: ${({ theme }) => theme.colors.textSubtle};
      }
    }
  }
`

const AprWrapper = styled.div`
  min-width: 60px;
  text-align: left;
`

const Apr: React.FC<AprProps> = ({
  value,
  lpLabel,
  quoteTokenAdresses,
  quoteTokenSymbol,
  tokenAddresses,
  cakePrice,
  originalValue,
  hideButton = false,
  farm,
}) => {
  const TranslateString = useI18n()
  const displayApr = value ? `${value}%` : TranslateString(656, 'Loading...')
  
  return (
    <Container>
      <AprWrapper>{displayApr}</AprWrapper>
      {!hideButton && (
        <ApyButton lpLabel={lpLabel}
          quoteTokenAdresses={quoteTokenAdresses}
          quoteTokenSymbol={quoteTokenSymbol}
          tokenAddresses={tokenAddresses}
          cakePrice={cakePrice}
          apy={originalValue}
        />
      )}
    </Container>
  )
}

export default Apr
