import React, { useEffect, useCallback, useRef, useState } from 'react'
import { Route, useRouteMatch, useLocation } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { provider } from 'web3-core'
import { orderBy } from 'lodash'
import { Image, Heading, Toggle, Text, RowType } from '@pancakeswap-libs/uikit'
import { BLOCKS_PER_YEAR, CAKE_PER_BLOCK, CAKE_POOL_PID } from 'config'
import FlexLayout from 'components/layout/Flex'
import { getBalanceNumber } from 'utils/formatBalance'
import Page from 'components/layout/Page'
import { useFarms, usePriceBnbBusd, usePriceCakeBusd } from 'state/hooks'
import useRefresh from 'hooks/useRefresh'
import { fetchFarmUserDataAsync } from 'state/actions'
import { QuoteToken } from 'config/constants/types'
import useI18n from 'hooks/useI18n'
import FarmCard, { FarmWithStakedValue } from './components/FarmCard/FarmCard'
import FarmTabButtons from './components/FarmTabButtons'
import Table from './components/FarmTable/FarmTable'
import { DesktopColumnSchema, ViewMode } from './components/types'
import ToggleView from './components/ToggleView/ToggleView'
import Select, { OptionProps } from './components/Select/Select'
import SearchInput from './components/SearchInput'
import { RowProps } from './components/FarmTable/Row'


export interface FarmsProps {
  tokenMode?: boolean
}

const BannerImage = styled.img`
  max-width: 300px;
  height: auto;
`

const ControlContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;

  justify-content: space-between;
  flex-direction: column;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 16px 5px;
  }
`

const ToggleWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-left: 10px;
  margin-right: 10px;

  ${Text} {
    margin-left: 8px;
  }
`

const LabelWrapper = styled.div`
  > ${Text} {
    font-size: 12px;
  }
`

const FilterContainer = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px 0px;

  ${({ theme }) => theme.mediaQueries.sm} {
    width: auto;
    padding: 0;
  }
`

const ViewControls = styled.div`
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  align-items: center;
  width: 100%;

  > div {
    padding: 8px 0px;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    justify-content: flex-start;
    width: auto;

    > div {
      padding: 0;
    }
  }
`

const Farms: React.FC<FarmsProps> = (farmsProps) => {
  const { path } = useRouteMatch()
  const { pathname } = useLocation()
  const TranslateString = useI18n()
  const farmsLP = useFarms()
  const cakePrice = usePriceCakeBusd()
  const bnbPrice = usePriceBnbBusd()
  const [query, setQuery] = useState('')
  const [viewMode, setViewMode] = useState(ViewMode.CARD) // ViewMode.CARD / TABLE
  const [sortOption, setSortOption] = useState('default')
  const { account, ethereum }: { account: string; ethereum: provider } = useWallet()
  const { tokenMode } = farmsProps;

  const handleChangeQuery = (event: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(event.target.value)
  }

  const handleSortOptionChange = (option: OptionProps): void => {
    setSortOption(option.value)
  }

  const dispatch = useDispatch()
  const { fastRefresh } = useRefresh()
  useEffect(() => {
    if (account) {
      dispatch(fetchFarmUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])

  const [stakedOnly, setStakedOnly] = useState(false)

  const activeFarms = farmsLP.filter((farm) => !!farm.isTokenOnly === !!tokenMode && farm.multiplier !== '0X')
  const inactiveFarms = farmsLP.filter((farm) => !!farm.isTokenOnly === !!tokenMode && farm.multiplier === '0X')

  const tableRef = useRef(null)

  const stackedOnlyFarms = activeFarms.filter(
    (farm) => farm.userData && new BigNumber(farm.userData.stakedBalance).isGreaterThan(0),
  )

  const sortFarms = (farms: FarmWithStakedValue[]): FarmWithStakedValue[] => {
    switch (sortOption) {
      case 'default':
        return farms.sort((farm1, farm2) => {
          if (farm1.pid === 1) {
            return -1
          }
          if (farm2.pid === 1) {
            return 1
          }
          if (farm1.pid === 0) {
            return -1
          }
          if (farm2.pid === 0) {
            return 1
          }
          if (farm1.pid === 39) {
            return -1
          }
          if (farm2.pid === 39) {
            return 1
          }
          if (farm1.pid === 36) {
            return -1
          }
          if (farm2.pid === 36) {
            return 1
          }
          if (farm1.pid === 34) {
            return -1
          }
          if (farm2.pid === 34) {
            return 1
          }
          if (farm1.pid === 32) {
            return -1
          }
          if (farm2.pid === 32) {
            return 1
          }
          if (farm1.pid === 29) {
            return -1
          }
          if (farm2.pid === 29) {
            return 1
          }
          return farm1.apy.isGreaterThan(farm2.apy) ? -1 : 1
        })
      case 'apr':
        return orderBy(farms, 'apy', 'desc')
      case 'multiplier':
        return orderBy(farms, (farm: FarmWithStakedValue) => farm.multiplier ? Number(farm.multiplier.slice(0, -1)) : 0, 'desc')
      case 'earned':
        return orderBy(farms, (farm: FarmWithStakedValue) => (farm.userData ? farm.userData.earnings : 0), 'desc')
      case 'liquidity':
        return orderBy(farms, (farm: FarmWithStakedValue) =>
          Number(new BigNumber(farm.quoteTokenPrice).times(new BigNumber(farm.lpTotalInQuoteToken)).toNumber()), 'desc')
      default:
        return farms
    }
  }

  // /!\ This function will be removed soon
  // This function compute the APY for each farm and will be replaced when we have a reliable API
  // to retrieve assets prices against USD
  const farmsList = useCallback(
    (farmsToDisplay): FarmWithStakedValue[] => {
      let farmsToDisplayWithAPY: FarmWithStakedValue[] = farmsToDisplay.map((farm) => {
        // if (!farm.tokenAmount || !farm.lpTotalInQuoteToken || !farm.lpTotalInQuoteToken) {
        //   return farm
        // }
        const cakeRewardPerBlock = new BigNumber(farm.fswapPerBlock || 1)
          .times(new BigNumber(farm.poolWeight))
          .div(new BigNumber(10).pow(18))
        const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR)

        // cakePriceInQuote * cakeRewardPerYear / lpTotalInQuoteToken
        let apy = cakePrice.times(cakeRewardPerYear)

        const totalValue = new BigNumber(farm.lpTotalInQuoteToken || 0).times(farm.quoteTokenPrice);

        // if (farm.quoteTokenSymbol === QuoteToken.BNB) {
        //   totalValue = totalValue.times(bnbPrice);
        // }

        if (totalValue.comparedTo(0) > 0) {
          apy = apy.div(totalValue);
        }

        return { ...farm, apy }
      })

      if (query) {
        const lowercaseQuery = query.toLowerCase()
        farmsToDisplayWithAPY = farmsToDisplayWithAPY.filter((farm: FarmWithStakedValue) => {
          if (farm.lpSymbol.toLowerCase().includes(lowercaseQuery)) {
            return true
          }

          return false
        })
      }
      return farmsToDisplayWithAPY
    },
    [query, cakePrice],
  )


  const isActive = !pathname.includes('history')
  let farmsStaked = []
  if (isActive) {
    farmsStaked = stakedOnly ? farmsList(stackedOnlyFarms) : farmsList(activeFarms)
  } else {
    farmsStaked = farmsList(inactiveFarms)
  }

  farmsStaked = sortFarms(farmsStaked)

  const rowData = farmsStaked.map((farm) => {
    const { quoteTokenAdresses, quoteTokenSymbol, tokenAddresses } = farm
    const lpLabel = farm.lpSymbol && farm.lpSymbol.toUpperCase().replace('PANCAKE', '')

    let totalValue: BigNumber
    if (!farm.lpTotalInQuoteToken) {
      totalValue = null
    }
    if (farm.quoteTokenSymbol === QuoteToken.BNB) {
      totalValue = bnbPrice.times(farm.lpTotalInQuoteToken)
    }
    if (farm.quoteTokenSymbol === QuoteToken.FSWAP) {
      totalValue = cakePrice.times(farm.lpTotalInQuoteToken)
    }
    totalValue = new BigNumber(farm.lpTotalInQuoteToken).times(farm.quoteTokenPrice)

    const row: RowProps = {
      apr: {
        value: farm.apy
          ? farm.apy && farm.apy.times(new BigNumber(100)).toNumber().toLocaleString('en-US').slice(0, -1)
          : null,
        multiplier: farm.multiplier,
        lpLabel,
        quoteTokenAdresses,
        quoteTokenSymbol,
        tokenAddresses,
        cakePrice,
        originalValue: farm.apy,
        farm,
      },
      farm: {
        image: farm.lpSymbol.split(' ')[0].toLocaleLowerCase(),
        label: lpLabel,
        pid: farm.pid,
      },
      earned: {
        earnings: farm.userData ? getBalanceNumber(new BigNumber(farm.userData.earnings)) : null,
        pid: farm.pid,
      },
      liquidity: {
        liquidity: Number(totalValue),
      },
      fee: {
        fee: farm.depositFeeBP,
      },
      multiplier: {
        multiplier: farm.multiplier,
      },
      details: farm,
    }

    return row
  })

  const renderContent = (): JSX.Element => {
    if (viewMode === ViewMode.TABLE && rowData.length) {
      const columnSchema = DesktopColumnSchema

      const columns = columnSchema.map((column) => ({
        id: column.id,
        name: column.name,
        label: column.normal,
        sort: (a: RowType<RowProps>, b: RowType<RowProps>) => {
          switch (column.name) {
            case 'farm':
              return b.id - a.id
            case 'apr':
              if (a.original.apr.value && b.original.apr.value) {
                return Number(a.original.apr.value) - Number(b.original.apr.value)
              }

              return 0
            case 'earned':
              return a.original.earned.earnings - b.original.earned.earnings
            default:
              return 1
          }
        },
        sortable: column.sortable,
      }))

      return <Table data={rowData} ref={tableRef} columns={columns} />
    }

    return (
      <div>
        <FlexLayout>
          <Route exact path={`${path}`}>
            {farmsStaked.map((farm) => (
              <FarmCard
                key={farm.pid}
                farm={farm}
                bnbPrice={bnbPrice}
                cakePrice={cakePrice}
                ethereum={ethereum}
                account={account}
                removed={false}
              />
            ))}
          </Route>
          <Route exact path={`${path}/history`}>
            {farmsStaked.map((farm) => (
              <FarmCard
                key={farm.pid}
                farm={farm}
                bnbPrice={bnbPrice}
                cakePrice={cakePrice}
                ethereum={ethereum}
                account={account}
                removed
              />
            ))}
          </Route>
        </FlexLayout>
      </div>
    )
  }

  return (
    <Page>
      <Heading as="h1" size="xl" color="primary" mb="10px" style={{ textAlign: 'center' }}>
        {
          tokenMode ?
            TranslateString(999, 'Pools')
            :
            TranslateString(999, 'Farms')
        }
      </Heading>
      <Heading as="h1" size="lg" color="secondary" mb="30px" style={{ textAlign: 'center' }}>
        {
          tokenMode ?
            TranslateString(999, 'Stake tokens to earn TMDS')
            :
            TranslateString(999, 'Stake LP tokens to earn TMDS')
        }
      </Heading>
      {/* <Heading as="h2" color="secondary" mb="50px" style={{ textAlign: 'center' }}>
        {TranslateString(999, 'Deposit fees will be used for TMDS buyback and burn')}
      </Heading> */}
      <ControlContainer>
        <ViewControls>
          <ToggleView viewMode={viewMode} onToggle={(mode: ViewMode) => setViewMode(mode)} />
          <ToggleWrapper>
            <Toggle checked={stakedOnly} scale="sm" onChange={() => setStakedOnly(!stakedOnly)} />
            <Text> {TranslateString(1116, 'Staked only')}</Text>
          </ToggleWrapper>
          <FarmTabButtons />
        </ViewControls>
        <FilterContainer>
          <LabelWrapper>
            <Text>SORT BY</Text>
            <Select
              options={[
                {
                  label: 'Default',
                  value: 'default',
                },
                {
                  label: 'APR',
                  value: 'apr',
                },
                {
                  label: 'Multiplier',
                  value: 'multiplier',
                },
                {
                  label: 'Earned',
                  value: 'earned',
                },
                {
                  label: 'Liquidity',
                  value: 'liquidity',
                },
                {
                  label: 'Fees',
                  value: 'fees',
                },
              ]}
              onChange={handleSortOptionChange}
            />
          </LabelWrapper>
          <LabelWrapper style={{ marginLeft: 16 }}>
            <Text>SEARCH</Text>
            <SearchInput onChange={handleChangeQuery} value={query} />
          </LabelWrapper>
        </FilterContainer>
      </ControlContainer>
      {renderContent()}
      {/* <div style={{ maxWidth: "100%", textAlign: 'center', marginTop: '25px' }}>
        <BannerImage src="/images/egg/8.png" alt="illustration" />
      </div> */}

    </Page>
  )
}

export default Farms
